import {Store, Parser} from "../_snowpack/pkg/n3.js";
import {MarkingsStore} from "./MarkingsStore.js";
export class TextSourceBase {
  constructor(project, settings) {
    this.#fileCache = new Map();
    this.#project = project;
    this.settings = settings;
  }
  #project;
  #fileCache;
  #markingsStore;
  get name() {
    return this.settings.name;
  }
  async getMarkingsStore() {
    if (this.#markingsStore)
      return this.#markingsStore;
    const store = new Store();
    const parser = new Parser();
    const {slug, branch} = this.#project;
    const sources = await Promise.all(this.settings.files.map(async (fileMeta) => {
      const url = `https://raw.githubusercontent.com/${slug}/${branch}/${fileMeta.file}`;
      if (this.#fileCache.has(url))
        return this.#fileCache.get(url);
      const response = await fetch(url);
      const text = await response.text();
      this.#fileCache.set(url, text);
      return text;
    }));
    const allPrefixes = {};
    for (const source of sources) {
      await parser.parse(source, (error, quad, prefixes) => {
        if (error)
          console.error(error);
        if (quad)
          store.addQuad(quad);
        else
          Object.assign(allPrefixes, prefixes);
      });
    }
    this.#markingsStore = new MarkingsStore(store, this.settings.book);
    return this.#markingsStore;
  }
}
